@import "utils";
@import "fonts";
@import "typography";

body {
  margin-top: 27px;
  max-width: 1440px;
}

.bg-red {
  background: #dc151d;
}

.container-1 {
  height: 41px;
  align-items: center;
  padding: 0;

}

.navbar {
  padding: 0;
}

.navbar-brand,
.nav-link-left {
  font-family: "Plumb", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #fff !important;
  margin: 0 !important;
  font-weight: normal !important;
  text-transform: none !important;
}

.nav-link-left {
  font-size: 13px !important;
  line-height: 15px !important;
  padding-left: 0.5rem !important;
  padding-right: 0.5rem !important;
}

.navbar-brand-left {
  padding-left: 35px;
}

.navbar-brand-img {
  padding-bottom: 4px;
  padding-right: 4px;
}

.nav-link {
  font-family: "Plumb", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000;
  padding-left: 1rem;
  padding-right: 1rem;
}

.container-2 {
  height: 89px;
  padding-left: 0;
  background: #fff;
}

.nav {
  align-items: center;
}

.socials {
  padding: 0 3px !important;
}

.breadcrumbs {
  margin-top: 26px;
  font-family: "Plumb", sans-serif;
  font-size: 14px;
  line-height: 26px;
  color: #a8a8a8;
}

.breadcrumbs-primary {
  font-weight: bold;
  text-decoration-line: underline;
  color: #000;
}

.breadcrumbs-secondary {
  font-weight: normal;
  color: #000;
}

.title {
  margin-top: 29px;
  font-family: "Plumb", sans-serif;
  font-weight: bold;
  font-size: 50px;
  line-height: 60px;
  color: #000;
}

.action,
.date {
  margin-top: 40px;
  font-family: "Plumb", sans-serif;
  font-size: 16px;
  line-height: 23px;
}

.action {
  color: #ed1c24;
}

.date {
  color: #a8a8a8;
}

.text {
  margin-top: 37px;
  font-family: "Plumb", sans-serif;
  font-size: 16px;
  line-height: 30px;
  text-align: justify;
  color: #000;
}

.text2 {
  padding: 0;
}

.col-md-8,
.col-6 {
  padding-left: 0;
}

.nav-link-logo {
  padding-left: 0;
}

.main-img {
  margin-top: 50px;
  width: 643px;
  height: 433px;
  margin-left: 15px;
}

@media screen and (max-width: 992px) {
  .bg-red {
    display: none;
  }

  .nav-link {
    font-size: 11px;
  }

  .justify-content-end {
    margin-left: 470px;
  }

  .nav-link-logo {
    padding-right: 32px;
  }

  .title {
    font-size: 30px;
    margin-top: 0;
    line-height: 40px;
  }

  .text {
    font-size: 14px;
  }

}

@media screen and (max-width: 768px) {
  .nav-link-logo {
    padding-right: 0;
  }

  .nav-link {
    font-size: 9px;
    padding-left: 0.5rem;
    padding-right: 0.3rem;
  }

  .justify-content-end {
    margin-left: 340px;
  }
}

@media screen and (max-width: 582px) {

  .nav {
    padding-left: 5px;
  }

  .container-3 {
    padding-right: 39px;
    padding-left: 29px;
  }

  .justify-content-end {
    margin-left: 320px;
  }
}

